$susy: (
  flow: ltr,
  math: fluid,
  output: float,
  gutter-position: after,
  container: 1152px,
  container-position: center,
  columns: 4,
  gutters: .25,
  column-width: false,
  global-box-sizing: content-box,
  last-flow: to,
);

$symmetric: (
  columns: 12,
  container: auto,
  gutters: .25,
  gutter-position: split,
  output: float,
/*   debug: (image: show), */
);

$columns16: (
  columns: 16,
  container: 1152px,
  gutters: .25,
  gutter-position: split,
  output: float,
/*   debug: (image: show), */
);

$full-width: (
  columns: 12,
  container: auto,
  gutters: 0,
  output: float,
/*   debug: (image: show), */
);
