$phone-upper-boundary: 600px;
$tablet-portrait-upper-boundary: 900px;
$tablet-landscape-upper-boundary: 1200px;
$desktop-upper-boundary: 1800px;

$susy-media: (
  phone-only:  (
    media: screen,
    max-width: #{$phone-upper-boundary - 1},
  ),
  tablet-portrait-up:  (
    media: screen,
    min-width: $phone-upper-boundary,
  ),
  tablet-landscape-up:  (
    media: screen,
    min-width: $tablet-portrait-upper-boundary,
  ),
  big-desktop-up:  (
    media: screen,
    min-width: $desktop-upper-boundary,
  )
);

